@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600;700&family=Outfit:wght@300;400;500;600;700&display=swap');

@tailwind base;
@tailwind components;
@tailwind utilities;

:root {
  /* Minimalist & Professional - "Manay Zen" Theme */
  --background: #fdfdfd;
  --foreground: #1e293b;

  --card: #ffffff;
  --card-foreground: #1e293b;

  --popover: #ffffff;
  --popover-foreground: #1e293b;

  --primary: #2d3e50;
  /* Dark slate from screenshot */
  --primary-foreground: #ffffff;

  --secondary: #f1f5f9;
  --secondary-foreground: #475569;

  --muted: #f8fafc;
  --muted-foreground: #64748b;

  --accent: #f1f5f9;
  --accent-foreground: #1e293b;

  --destructive: #991b1b;
  --destructive-foreground: #ffffff;

  --border: #e2e8f0;
  --input: #e2e8f0;
  --ring: #2d3e50;

  --radius: 0.5rem;
}

@layer base {
  * {
    @apply border-border;
  }

  body {
    @apply bg-[var(--background)] text-[var(--foreground)];
    font-family: 'Inter', sans-serif;
    background-color: #f1f5f9;
    /* Subtle background from screenshot */
  }

  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    font-family: 'Outfit', sans-serif;
  }
}

@layer utilities {
  .ana-card {
    @apply bg-white shadow-sm border border-[#e2e8f0] max-w-2xl mx-auto p-8 md:p-12;
    border-radius: 4px;
  }

  .dotted-box {
    @apply border-2 border-dashed border-[#e2e8f0] rounded-md p-6 bg-[#fafafa];
  }

  .btn-ana {
    @apply bg-[#2d3e50] text-white hover:bg-[#1e293b] transition-colors rounded-sm py-3 px-6 font-semibold uppercase tracking-wider text-sm;
  }

  .input-ana {
    @apply w-full border border-[#cbd5e1] rounded-sm py-3 px-4 focus:ring-2 focus:ring-[#2d3e50] transition-shadow bg-white;
  }
}